<template>
  
    <div class="contenedor">
      <div class="contenedor-lista">
        <Modal/>
        <div class="lista">
            <v-data-table
            :headers="headers"
            :items="desserts"
            :page.sync="page"
            :items-per-page="itemsPerPage"
            hide-default-footer
            class="elevation-1"
            @page-count="pageCount = $event"
            >
            <template v-slot:item.image="{ item }">
                <img class="img-alu" :src="item.image" height="60px">
            </template>
            <template v-slot:item.action="{ item }">
            <v-icon
                small
                class="mr-2"
                @click="editItem(item)"
            >
                edit
            </v-icon>
            <v-icon
                small
                @click="deleteItem(item)"
            >
                delete
            </v-icon>
            </template>
            </v-data-table>
            <div class="text-center pt-2">
            <v-pagination v-model="page" :length="pageCount"></v-pagination>
            </div>
        </div>
      </div>
    </div>
  
</template>

<script>
import  Modal from './Modal';
import {mapState, mapActions} from 'vuex';
export default {
    components: {
        Modal
    },
    data: () => ({
        page: 1,
        pageCount: 0,
        itemsPerPage: 10,
        
    }),
    computed: {
        ...mapState(['desserts', 'headers'])
    }
};
</script>

